body {
  background-color: #131a20;
    background-position: top center;
    background-repeat: no-repeat;
    color: #666f80 !important;
    font-family: "Roboto", "Open Sans", sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: normal;
}
.container {
  background: white;
  border-radius: 5px;
  padding: 30px;
}
.breadcrumbs {
  padding-bottom: 20px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ebebeb;
}
.page figure {
  margin-bottom: 30px;
}
figure {
  margin: 0;
}

.page img {
  max-width: 100%;
}
img {
  border: 0;
}

.page .movie-title {
  font-size: 36px;
  font-size: 2.4em;
  font-weight: 300;
}